import { Directive, HostListener, ElementRef } from '@angular/core';

@Directive({
    selector: '[video-load]'
})

export class VideoDirective {
    
    constructor(
        private el: ElementRef
    ) { }

    @HostListener('loadstart') cargando() {
        this.el.nativeElement.currentTime = 0.3;
    }
}
